import React, { Component } from 'react';
import Link from 'next/link';
import Button from '../buttons/button';
import Logo from '../logo-2/logo';
import './header.scss';
import classnames from 'classnames';
import PopOver from '../btn-resp/popOver';
import Notifications from '../notifications/notifications';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { onNav } from '../../store/actions';

class Header extends Component{
  constructor(props){
    super(props);
    this.state = {
      down: false
    }
  }

  componentDidMount() {
    window.onscroll = () => {
      if(window.scrollY > 50) {
        this.setState({ down: true });
      } else {
        this.setState({ down: false });
      }
    }
  }

  render(){
    const { down } = this.state;
    const { isLogin } = this.props;
    return(
      <header className={classnames({
        scroll_header: down,
        header_hero_main: true,
        shadow: down,
      })}>
        <div className="container-fluid header_main_cont">
          <Logo />
          <nav>
            {/*
              isLogin 
              ? <Notifications />
              : <Button color="light">Login</Button>
            */}
            <Notifications />
            <Button color="light">Login</Button>
            <button onClick={props.onNav} id="btn-resp" type="button">
              {iconBars}
            </button>
            <PopOver popOpen={props.nav} />
          </nav>
        </div>
      </header>
    );
  }
};

const mapDispacthToProps = dispatch => bindActionCreators({onNav}, dispatch);
const mapStateToProps = state => ({ isLogin: state.login });
export default connect(mapStateToProps, mapDispacthToProps)(Header);

